<script lang="ts">
export default {
  props: {
    id: {
      type: Number,
      required: true,
    },
    value: {
      type: String,
      required: true,
    },
    renderedDimensions: {
      type: Number,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
    styles: {
      type: Object,
      default: () => ({}),
    },
    visible: {
      type: Boolean,
      default: true,
    },
    isActive: {
      type: Boolean,
      default: false,
    },
    isEditable: {
      type: Boolean,
      default: true,
    },
  },
  emits: { input: null },
  computed: {
    reactiveFontSize() {
      return this.styles.fontSize * this.renderedDimensions
    },
  },
  methods: {
    update(event: Event) {
      console.log('update', event)
      const target = event.target as HTMLInputElement
      console.log('target', target)
      this.$emit('input', target.value)
    },
  },

}
</script>

<template>
  <div
    :ref="`textarea${id}`"
    :key="`textarea${id}text`"
    :disabled="!isEditable"
    tabindex="-1"
    cols="1"
    rows="1"
    :style="{
      resize: 'none',
      whiteSpace: 'pre',
      fontFamily: styles.fontFamily,
      fontSize: `${reactiveFontSize}px`,
      fontStyle: styles.isItalic ? 'italic' : 'normal',
      fontWeight: styles.isBold ? 'bold' : 'normal',
      textDecoration: styles.isUnderlined ? 'underline' : 'none',
      textAlign: styles.textAlign,
      color: styles.fontColor,
      opacity: 'visible' in styles && !styles.visible ? '0' : '1',
      margin: '0.5rem',
    }"
  >
    <input
      style="display: none"
      :value="value"
      @input="update($event)"
    />
    {{ value }}
    {{ isEditable }}
  </div>
</template>
