    IconJustifyLeft,
    IconJustifyCenter,
    IconJustifyRight,
    IconSpacing,
    IconWarp,
    /*         IconArrangeFront,
        IconArrangeBack, */
  },
  data() {
    return {
      fontFamily: 'Montserrat',
      inputMode: false,
      pageHeight: screen.availHeight - 16 * (4 + 3 + 3.5 + 3 * 5),
      spacingVisible: false,
      warpingVisible: false,
      customTemplate: { 'wallid.io': true },
    }
  },
  computed: {
    ...mapState(useAccountStore, ['adminEmail']),
    ...mapState(useTemplateStore, [
      'activeComponent',
      'renderedDimensions',
      'selectedTab',
      'layoutBackgroundColor',
    ]),
    ...mapWritableState(useTemplateStore, ['layoutBackgroundColor']),
    activeComponent: {
      get() {
        return useTemplateStore().activeComponent
      },
      set(value) {
        useTemplateStore().setActiveComponent(value)
      },
    },
    activeComponentStyles: {
      get() {
        return this.activeComponent?.styles
      },
      set(value) {
        this.activeComponent.styles = value
        // console.log('activeComponentStyles:set', value)
        // this.$store.commit('setActiveComponent', {
        //   ...this.activeComponent,
        //   styles: value,
        // })
      },
    },
  },
  methods: {
    isText() {
      return this.activeComponent?.type === 'text'
    },
    isSignature() {
      return this.activeComponent?.type === 'signature'
    },
    showFeature() {
      return (
        this.adminEmail
        && this.customTemplate[this.getEmail(this.adminEmail).domain]
      )
    },
  },
}
</script>

