<script>
import { useTemplateStore } from '@/store'
import { mapWritableState } from 'pinia'

import IconAddPage from '../../assets/icon-add-page.vue'
import IconDelete from '../../assets/icon-delete-attribute.vue'
import Page from '../Canvas/Page.vue'
import Toolbar from '../Canvas/Toolbar.vue'
import Hover from '../Misc/Hover.vue'

import { BASE_WIDTH, DEFAULT_ACTIVE_COMPONENT, QR_CODE_COMPONENT } from '@/store/TemplateStore'

export default {
  components: {
    Toolbar,
    IconAddPage,
    IconDelete,
    Page,
    Hover,
  },
  props: {
    editable: {
      default: true,
    },
    width: {
      default: screen.availHeight - 16 * (4 + 3 + 3.5 + 3 * 5),
    },
    snapshot: {
      default: false,
    },
  },
  data() {
    return {
      /* 16 - convertion rem to px; heights: 4 - header, 3 - canvas toolbar, 3.5 - footer, 3 * 5 - passepartout padding and add-page height and margin, 2 - border corrections and ??? */
      pageHeight: screen.availHeight - 16 * (4 + 3 + 3.5 + 3 * 5),
      iconColorFront: '',
      iconColorBack: '',
      displayDeleteHover: false,
      displayClearHover: false,
      BASE_WIDTH,
    }
  },
  computed: {
    ...mapState(useTemplateStore, [
      'template',
      'currentLayout',
      'layoutBackgroundColor',

    ]),
    ...mapWritableState(useTemplateStore, [
      'initialWidth',
      'components',
      'activeComponent',
      'layoutBackgroundColor',
      'renderedDimensions',
      'backSide',
      'activeTemplateSide',

    ]),
  },
  watch: {
    backSide(newBackSide) {
      if (newBackSide === true) {
        /* Not an ideal solution to scroll, but it forces the code to run after render */
        setTimeout(() => {
          this.$refs.back.scrollIntoView({ behavior: 'smooth' })
        }, 100)
      }
    },
    components(value) {
      console.log('components:value changed', value)
    },
    currentLayout() {
      this.calculateRenderedDimensions()
    },
  },
  created() {
    window.addEventListener('resize', this.calculateRenderedDimensions)
  },
  mounted() {
    this.calculateRenderedDimensions()
  },
  unmounted() {
    window.removeEventListener('resize', this.calculateRenderedDimensions)
  },
  methods: {
    calculateRenderedDimensions() {
      const pageHeight = this.$refs.page.getBoundingClientRect().height
      const pageWidth = this.$refs.page.getBoundingClientRect().width
      const passepartoutHeight = this.$refs.passepartout.getBoundingClientRect().height
      const passepartoutWidth = this.$refs.passepartout.getBoundingClientRect().width
      console.log('pageHeight', pageHeight)
      console.log('pageWidth', pageWidth)
      console.log('passepartoutHeight', passepartoutHeight)
      console.log('passepartoutWidth', passepartoutWidth)
      console.log('currentLayout', this.currentLayout)

      this.initialWidth = Math.min(
        passepartoutHeight,
        passepartoutWidth,
      ) - 50
      console.log('initialWidth', this.initialWidth)

      // percentage of the ratio of the page height to the page width
      this.renderedDimensions = (this.initialWidth / BASE_WIDTH) //* 100

      console.log('renderedDimensions', this.renderedDimensions)
    },
    clearActiveComponent() {
      console.log('clearActiveComponent')
      if (this.activeComponent?.isEditable) {
        this.activeComponent.isEditable = false
      }
      useTemplateStore().setActiveComponent()
    },
    clearPage() {
      this.template.idFront = 0
      this.template.backgroundFront = ''
      /* how to splice only frontside components and avoid qrcode */
      if (this.components?.length > 0) {
        this.components = this.template.components.filter(component => component.type !== 'qrcode' && component.templateSide === 'back')
      }

      // this.components.push(QR_CODE_COMPONENT)
      this.template.backgroundFront = ''
      useTemplateStore().setActiveComponent(DEFAULT_ACTIVE_COMPONENT)
    },
    deletePage() {
      this.template.idBack = 0
      this.template.backgroundBack = ''
      /* how to splice only backside components */
      function isFront(component) {
        return component.templateSide === 'front'
      }

      if (this.components) {
        this.components = this.components.filter(isFront)
      }

      this.template.backgroundBack = ''
      useTemplateStore().setActiveComponent(DEFAULT_ACTIVE_COMPONENT)
      this.backSide = false
    },
    /* these changecolor methods are not requires and iconColor data can be directly changed in the DOM */
    setCurrentSide() {
      const scrollHeight = this.$refs.passepartout.scrollHeight
      const currentHeight
        = this.$refs.passepartout.scrollTop
        + this.$refs.passepartout.offsetHeight
      if (this.backSide) {
        if (currentHeight < (scrollHeight * 3) / 4) {
          this.activeTemplateSide = 'front'
        } else {
          this.activeTemplateSide = 'back'
        }
      } else {
        this.activeTemplateSide = 'front'
      }
      console.log(this.activeTemplateSide)
    },
  },
}
</script>

<template>
  <div class="canvas">
    <div v-if="editable" class="options">
      <Toolbar />
    </div>
    <div
      ref="passepartout"
      class="passepartout"
      @scroll="setCurrentSide()"
      @mousedown.self="clearActiveComponent()"
    >
      <div
        :id=" snapshot ? 'template-canvas' : 'page'"
        ref="page"
        class="page"
        :style="!editable ? 'pointer-events: none; user-select: none; ' : ''"
      >
        <Page side="front" :initial-width="initialWidth" :editable="editable" :rendered-dimensions="renderedDimensions" />
        <div
          v-if="editable && currentLayout"
          class="delete-page"
          style="
            display: flex;
            overflow: visible !important;
            white-space: nowrap;
            transform: translateX(125%);
          "
          @click="clearPage()"
          @mouseover="iconColorFront = 'red', (displayClearHover = true)"
          @mouseout="iconColorFront = '', (displayClearHover = false)"
        >
          <IconDelete :color="iconColorFront" />
          <Hover v-show="displayClearHover">
            {{ $t('canvas.clearPage') }}
          </Hover>
        </div>
      </div>

      <div
        v-show="!backSide && editable && currentLayout === 'A4'"
        class="add-page"
        :style="{
          minWidth: `${renderedDimensions * BASE_WIDTH}px`,
          color: '#373c43',
        }"
        @click="backSide = true "
      >
        <IconAddPage style="margin-right: 0.5rem" />{{
          $t('canvas.addBackside')
        }}
      </div>

      <div
        v-show="backSide"
        id="back"
        ref="back"
        style="margin-top: 1.25rem; margin-bottom: 5rem"
        class="page"
        :style="!editable ? 'pointer-events: none; user-select: none; ' : ''"
      >
        <Page side="back" :initial-width="initialWidth" :editable="editable" />
        <div
          v-if="editable"
          class="delete-page"
          style="
            display: flex;
            overflow: visible !important;
            white-space: nowrap;
            z-index: 1;
          "
          :style="{
            transform: 'translateX(125%)',
          }"
          @click="deletePage()"
          @mouseover="iconColorBack = 'red', (displayDeleteHover = true)"
          @mouseout="iconColorBack = '', (displayDeleteHover = false)"
        >
          <IconDelete :color="iconColorBack" />
          <Hover v-show="displayDeleteHover">
            {{ $t('canvas.deletePage') }}
          </Hover>
        </div>
      </div>
    </div>
  </div>
</template>

