  top: 0;
  display: flex;
  min-height: 3rem;
  max-height: 3rem;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  /* overflow: visible !important; */
}

.undo-redo {
  display: flex;
  justify-content: space-between;
  padding: 0 1.25rem;
  min-width: 6rem;
}

.passepartout {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  padding-top: 5rem;
  background-color: #e2e2e2;
  border-left: 1px solid transparent;
  overflow-y: scroll !important;
}

.passepartout::-webkit-scrollbar {
  display: contents;
