<script>
import { useTemplateStore } from '@/store'
import { mapState } from 'pinia'

import CurvedText from './CurvedText.vue'
import ImageComponent from './Image.vue'
import InputComponent from './Input.vue'
import QRCode from './QRCode.vue'
import SignatureComponent from './Signature.vue'
import TextComponent from './Text.vue'

export default {
  name: 'Attribute',
  components: {
    CurvedText,
    ImageComponent,
    InputComponent,
    QRCode,
    SignatureComponent,
    TextComponent,
  },
  props: {
    /** @type {import('vue').PropOptions<import('../../../types/types').TemplateElement>}   */
    value: {
      type: Object,
      // required: true,
      default: () => ({
        type: 'text',
        text: '',
        x: 0,
        y: 0,
        fontSize: 0,
        textAlign: 'center',
        warp: 0,
        id: 0,
      }),
    },
  },
  emits: ['input'],
  computed: {
    ...mapState(useTemplateStore, ['renderedDimensions', 'initialWidth', 'backSide']),
    reactiveValue: {
      get() {
        return this.value
      },
      set(value) {
        console.log(value)
        this.$emit('input', value)
      },
    },
  },
  watch: {
    value(newValue) {
      console.log('Attribute:watch:value', newValue)
    },

  },
  mounted() {
    console.log('Attribute:mounted:templateElement', this.reactiveValue)
  },
  methods: {
    /**
     * This function is used to resize the text area when the text is changed and keep it within the bounds of the page.
     */
    resizeTextArea(textAlign = true) {
      if (this.reactiveValue.type === 'text') {
        console.log('resizeTextArea:this.reactiveValue', this.reactiveValue.x, textAlign, this.$refs, this.$refs[`${this.reactiveValue.id}`])
        const attributeWidth = Number.parseInt(
          this.$refs[`${this.reactiveValue.id}`].scrollWidth,
        )

        if (!this.reactiveValue.previousWidth || Number.isNaN(this.reactiveValue.previousWidth)) {
          this.reactiveValue.previousWidth = attributeWidth
        }
        console.log('isNaN.previousWidth', this.reactiveValue.previousWidth, this.$refs[`${this.reactiveValue.id}`].scrollWidth, this.$refs[`${this.reactiveValue.id}`].getBoundingClientRect().width)

        // if (textAlign) {
        //   console.log('initialWidth', attributeWidth, this.reactiveValue.x)
        //   this.reactiveValue.x = this.reactiveValue.x + (this.reactiveValue.previousWidth - attributeWidth) / 2
        //   console.log('this.reactiveValue.x', this.reactiveValue.x)
        // }

        // console.log('previousWidth', this.reactiveValue.previousWidth)

        if (textAlign) {
          const delta
            = (100 * (attributeWidth - this.reactiveValue.previousWidth))
            / (this.renderedDimensions * this.initialWidth)

          if (this.reactiveValue.textAlign === 'left') {
            this.reactiveValue.x = Math.abs(this.reactiveValue.x + delta)
          } else if (this.reactiveValue.textAlign === 'right') {
            this.reactiveValue.x = Math.abs(this.reactiveValue.x - delta)
          }
          // console.log('delta', delta, this.initialWidth, this.reactiveValue.x)
        }

        this.reactiveValue.previousWidth = Math.min(attributeWidth, this.initialWidth)
      }
    },

    /**
     *
     * @param component {import('../../../types/types').TemplateElement}
     */
    getComponent({ type, warp, isEditable, text, id, isDraggable }) {
      console.log('getComponent:type', type, text, id, isDraggable, !isEditable)

      // especial cases like curved-text
      if (type === 'text'
        && !Number.isNaN(warp)
        && (warp && warp !== 0)
        && !isEditable) {
        console.log('CurvedText')
        return 'CurvedText'
      }

      switch (type) {
        case 'image':
          return 'ImageComponent'
        case 'input':
          return 'InputComponent'
        case 'qrcode':
          return 'QRCode'
        case 'signature':
          return 'SignatureComponent'
        case 'text':
          return 'TextComponent'
        default:
          return 'div'
      }
    },
    changeActiveComponent(event, component) {
      console.log('changeActiveComponent', event, component, this.$refs[component.id])
      const xDistanceMouseToComponentCenter
        = event.clientX
        - this.$refs[component.id].getBoundingClientRect().x
        - this.$refs[component.id].getBoundingClientRect().width / 2
      const yDistanceMouseToComponentCenter
        = event.clientY
        - this.$refs[component.id].getBoundingClientRect().y
        - this.$refs[component.id].getBoundingClientRect().height / 2

      useTemplateStore().setXYDistanceMouseToComponentCenter({
        x: xDistanceMouseToComponentCenter,
        y: yDistanceMouseToComponentCenter,
      })
      this.reactiveValue.isDraggable = true
      this.setActiveComponent(component)
    },

    updateComponent(component) {
      console.log('updateComponent:component', component, this.reactiveValue)
      if (!component.isDraggable) {
        return
      }

      if (component.type === 'image' && component.isEditable) {
        component.size
          = this.$refs[`${component.id}`].getBoundingClientRect().width
          / this.renderedDimensions
        console.log(component.size)
      }
      console.log('updateComponent:component:setDraggable:false')
      this.reactiveValue.isDraggable = false

      // change side bugs when mouse goes to fast throught page and canvas where moving function does not run
      if (component.templateSide === 'front' && component.y > 100) {
        if (this.backSide) {
          component.templateSide = 'back'
        } else {
          this.deleteComponent(component)
        }
      } else if (component.templateSide === 'back' && component.y < 0) {
        component.templateSide = 'front'
      }

      if (
        component.x > 100
        || component.x < 0
        || component.y > 100
        || component.y < 0
      ) {
        this.deleteComponent(component)
      }
    },
  },

}
</script>

<template>
  <div
    :ref="reactiveValue.id"
    @mousedown.capture="changeActiveComponent($event, reactiveValue)"
    @mouseup="updateComponent(reactiveValue)"
  >
    {{ reactiveValue.isEditable }}
    <component
      v-bind="reactiveValue"
      :is="getComponent(reactiveValue)"
      :rendered-dimensions="renderedDimensions"
      :styles="reactiveValue.styles"
      :value="reactiveValue.text"
      @input="(value) => { reactiveValue.text = value; resizeTextArea() }"
    />
  </div>
</template>

