<script>
import { useTemplateStore } from '@/store'
import { mapState, mapWritableState } from 'pinia'

import debug from 'debug'

import IconDelete from '../../assets/icon-delete-attribute'
import IconEdit from '../../assets/icon-edit-attribute'
import IconInfo from '../../assets/icon-info-big'
import Hover from '../Misc/Hover.vue'

import Attribute from './Attributes/Attribute.vue'
/* import A4 from "../../assets/Layouts/rectangle-a4";
import Badge from "../../assets/Layouts/badge-circular"; */

import { layoutRatios } from '@/constants'

const logDebug = debug('TemplateEditor:Page')

const GRID_MOVEMENT_UNITS = 0.5

export default {
  components: {
    IconDelete,
    IconEdit,
    IconInfo,
    Hover,
    Attribute,
    /*     A4,
    Badge */
  },
  props: ['pageSide', 'editable', 'renderedDimensions'],
  data() {
    return {
      /* 16 - convertion rem to px; heights: 4 - header, 3 - canvas toolbar, 3.5 - footer, 3 * 5 - passepartout padding and add-page height and margin, 2 - border corrections and ??? */
      iconColorEdit: '',
      iconColorDelete: '',
      displayQRCodeHover: false,
      displayDeleteHover: false,
      displayEditHover: false,
    }
  },
  computed: {
    // calcWidthPage() {
    //   return `${Math.floor(this.pageHeight * layoutRatios[this.currentLayout])}px`
    // },
    ...mapState(useTemplateStore, [
      'currentLayout',
      'layoutBackgroundColor',
      'xDistanceMouseToComponentCenter',
      'yDistanceMouseToComponentCenter',
      'activeComponent',
      'initialWidth',
    ]),
    ...mapWritableState(useTemplateStore, ['components']),
    ...mapState(useTemplateStore, {
      currentSideBackground: state => this.pageSide === 'front' ? state.template.backgroundFront : state.template.backgroundBack,
    }),

    calcHeightPage() {
      return `${Math.floor(this.initialWidth / layoutRatios[this.currentLayout])}px`
    },
    activeComponentWarp() {
      return this.activeComponent?.warp
    },
    componentsMappedPageSide: {
      get() {
        return this.components.filter(
          component => component.templateSide === this.pageSide,
        )
      },
      set(value) {
        // TODO: check if this is correct
        this.components = this.components.map((component) => {
          if (component.templateSide === this.pageSide) {
            return value.find(v => v.id === component.id)
          }
          return component
        })
      },
    },

    pageHeight() {
      return this.initialWidth // - (16 * (4 + 3 + 3.5 + (3 * 5)))
    },
  },
  watch: {
    activeComponent(newVal, oldVal) {
      this.iconColorEdit = ''

      if (oldVal.text === '') {
        this.deleteComponent(oldVal)
      }
    },
  },

  mounted() {
    // for (let i = 0; i < this.components.length; i++) { this.resizeTextArea(this.components[i], true) }
  },
  updated() {
    // for (let i = 0; i < this.components.length; i++) { this.resizeTextArea(this.components[i], true) }
  },
  methods: {
    clearActiveComponent() {
      logDebug('clearActiveComponent')
      if (this.activeComponent?.isEditable) {
        this.activeComponent.isEditable = false
      }
      useTemplateStore().setActiveComponent()
    },
    // Recursivity out of control
    componentHeight(component) {
      logDebug('componentHeight:component', component, this.$refs[`curvedtext${component.id}0`])
      if (
        this.isText(component)
        && component.warp !== 0
        && this.$refs[`curvedtext${component.id}0`]
        && component.isEditable
      ) {
        const height
          = this.$refs[
            `curvedtext${component.id}0`
          ][0].getBoundingClientRect().height
        return height >= 60 ? `${height}px` : '3.65rem'
      }
      return 'unset'
    },
    componentWidth(component) {
      if (
        this.isText(component)
        && component.warp !== 0
        && this.$refs[`curvedtext${component.id}0`]
        && component.isEditable
      ) {
        const width
          = this.$refs[
            `curvedtext${component.id}0`
          ][0].getBoundingClientRect().width
        return width >= 60 ? `${width}px` : '3.65rem'
      }
      return '3.65rem'
    },
    warpAligner(characterId, numberCharacters, warp) {
      let delta
      if (numberCharacters.length % 2 === 0) {
        if (characterId === numberCharacters / 2) {
          delta = characterId - 0.5
        } else if (characterId === 1 + numberCharacters / 2) {
          delta = characterId + 0.5
        } else {
          delta = characterId + 0.5 - numberCharacters.length / 2
        }
      } else {
        delta = characterId - Math.floor(numberCharacters.length / 2)
      }
      return warp > 0 ? delta : -delta
    },
    characters(string) {
      const characters = string.split('')
      const indexedCharacters = []
      for (let i = 0; i < characters.length; i++) {
        indexedCharacters.push({ character: characters[i], id: i })
      }

      return indexedCharacters
    },

    moveActiveComponent(event) {
      /* should use filter instead? */
      // logDebug('moveActiveComponent:event', event, this.activeComponent, this.activeComponent.isDraggable)
      if (!this.activeComponent.isDraggable) {
        return
      }

      const component = this.activeComponent
      // = this.template.components[
      //   this.template.components.findIndex(
      //     component => component.id === this.activeComponent.id,
      //   )
      // ]
      if (
        component
        && component.type !== 'qrcode'
        && component.templateSide === this.pageSide
      ) {
        // set component position x and y coordinates
        component.x
            = Math.round(
            (100
            * (event.clientX
            - this.$refs.page.getBoundingClientRect().x
            - this.xDistanceMouseToComponentCenter))
            / this.$refs.page.getBoundingClientRect().width,
          ) < 0
            ? 0
            : Math.round(
              (100
              * (event.clientX
              - this.$refs.page.getBoundingClientRect().x
              - this.xDistanceMouseToComponentCenter))
              / this.$refs.page.getBoundingClientRect().width,
            ) > 100
              ? 100
              : Math.round(
                (100
                * (event.clientX
                - this.$refs.page.getBoundingClientRect().x
                - this.xDistanceMouseToComponentCenter))
                / this.$refs.page.getBoundingClientRect().width,
              )

        component.y
            = Math.round(
            (100
            * (event.clientY
            - this.$refs.page.getBoundingClientRect().y
            - this.yDistanceMouseToComponentCenter))
            / this.$refs.page.getBoundingClientRect().height,
          ) < 0
            ? 0
            : Math.round(
              (100
              * (event.clientY
              - this.$refs.page.getBoundingClientRect().y
              - this.yDistanceMouseToComponentCenter))
              / this.$refs.page.getBoundingClientRect().height,
            ) > 100
              ? 100
              : Math.round(
                (100
                * (event.clientY
                - this.$refs.page.getBoundingClientRect().y
                - this.yDistanceMouseToComponentCenter))
                / this.$refs.page.getBoundingClientRect().height,
              )
      }
    },
    isActiveComponent(component) {
      return component.id === this.activeComponent.id
    },
    setActiveComponent(event, component) {
      // component.isEditable ? null : (component.isDraggable = true)
      logDebug('setActiveComponent:component', component, this.activeComponent)
      component.isDraggable = !component.isEditable

      /* Prevent current activeComponent from remaining editable after switching to the new one */
    },
    editComponent(component) {
      component.isEditable = !component.isEditable
      if (component.isEditable) {
        this.iconColorEdit = 'blue'
      } else {
        this.iconColorEdit = ''
      }

      // this.components.isDraggable = false
    },
    isText(component) {
      return component.type === 'text'
    },
    isQRCode(component) {
      return component.type === 'qrcode'
    },
    deleteComponent(component) {
      this.iconColorDelete = ''
      this.displayDeleteHover = false
      useTemplateStore().deleteComponent(component)
    },
    reactiveFontSize(fontSize) {
      return (this.renderedDimensions * fontSize) // this.initialWidth
    },
    /**
     *
     * @param event {KeyboardEvent}
     */
    keyboardActiveComponent(event) {
      const value = event.key
      if (this.activeComponent) {
        switch (value) {
          case 'ArrowUp':
            this.activeComponent.y -= GRID_MOVEMENT_UNITS
            break
          case 'ArrowDown':
            this.activeComponent.y += GRID_MOVEMENT_UNITS
            break
          case 'ArrowLeft':
            if (!this.activeComponent.isEditable) {
              this.activeComponent.x -= GRID_MOVEMENT_UNITS
            }

            break
          case 'ArrowRight':
            if (!this.activeComponent.isEditable) {
              this.activeComponent.x += GRID_MOVEMENT_UNITS
            }
            break
          case 'Delete':
          case 'Backspace':
            if (!this.activeComponent.isEditable) {
              this.deleteComponent(this.activeComponent)
            }
            break
          default:
            break
        }
      }
    },
  },
}
</script>

<template>
  <div
    ref="page"
    class="page"
    :style="{
      // minWidth: calcWidthPage,
      // maxWidth: calcWidthPage,

      minWidth: `${initialWidth}px`,
      maxWidth: `${initialWidth}px`,

      minHeight: calcHeightPage,
      maxHeight: calcHeightPage,
      // minHeight: `${initialWidth}px`,
      // maxHeight: `${initialWidth}px`,

      backgroundColor: layoutBackgroundColor,
      backgroundImage: `url(${background()})`,
      backgroundSize: currentLayout === 'Card' ? 'contain' : 'cover',
      backgroundPosition: 'center center',
      borderRadius: currentLayout === 'Badge' ? '50%' : '0%',
      visibility: currentLayout ? 'visible' : 'hidden',
      userSelect: activeComponent ? 'auto' : 'none',

    }"
    tabindex="0"
    @mousedown.self="clearActiveComponent()"
    @mousemove="moveActiveComponent"
    @keydown="keyboardActiveComponent($event)"
  >
    <!-- Layout -->
    <!--     <component
      :is="currentLayout"
      :highlight="false"
      :background="layoutBackground"
      :style="{
        maxHeight: this.pageHeight + 'px'
      }"
    ></component> -->

    <!-- Components -->
    {{ pageHeight }}:{{ initialWidth }}
    <div
      v-for="(component, index) in componentsMappedPageSide"
      :id="component.id"
      :key="`component${component.id}`"
      :ref="`${component.id}`"
      class="component"
      :style="{
        top: `${component.y}%`,
        left: `${component.x}%`,
        padding: '0.5rem',
        // minHeight: componentHeight(component),
        // minWidth: componentWidth(component),
        zIndex: isQRCode(component) ? components.length : index,
      }"
      :class="{ activeComponent: isActiveComponent(component) }"
      @dblclick="(component.isEditable = true), (iconColorEdit = 'blue')"
    >
      <div
        v-show="isActiveComponent(component)"
        class="component-options"
        @mousedown="$event.stopPropagation()"
      >
        <!-- Delete component icon -->
        <div
          v-if="!isQRCode(component)"
          style="display: flex; overflow: visible !important; z-index: 12"
          @click="deleteComponent(component)"
          @mouseover="iconColorDelete = 'blue'"
          @mouseout="iconColorDelete = ''"
        >
          <div
            style="
              display: flex;
              position: relative;
              overflow: visible !important;
            "
            @mouseover="displayDeleteHover = true"
            @mouseout="displayDeleteHover = false"
          >
            <IconDelete :color="iconColorDelete" />
            <Hover v-show="displayDeleteHover">
              {{ $t('page.delete') }}
            </Hover>
          </div>
        </div>
        <!-- Edit component icon SHOULD USE === instead = 6 lines under -->
        <div
          v-if="!isQRCode(component)"
          style="display: flex; overflow: visible !important; z-index: 12"
          @click="editComponent(component)"
          @mouseover="iconColorEdit = 'blue'"
          @mouseout="
            iconColorEdit = component.isEditable
              ? (iconColorEdit = 'blue')
              : (iconColorEdit = '')
          "
        >
          <div
            style="
              display: flex;
              position: relative;
              overflow: visible !important;
              margin-left: 0.5rem;
            "
            @mouseover="displayEditHover = true"
            @mouseout="displayEditHover = false"
          >
            <IconEdit :color="iconColorEdit" />
            <Hover v-show="displayEditHover">
              {{ $t('page.edit') }}
            </Hover>
          </div>
        </div>
        <!-- Info QRCode icon -->
        <div
          v-if="isQRCode(component)"
          style="display: flex; overflow: visible !important; z-index: 15"
        >
          <div
            style="
              display: flex;
              position: relative;
              overflow: visible !important;
            "
            @mouseover="displayQRCodeHover = true"
            @mouseout="displayQRCodeHover = false"
          >
            <IconInfo />
            <Hover v-show="displayQRCodeHover">
              <p v-html="$t('page.qrcode')" />
            </Hover>
          </div>
        </div>
      </div>

      <!-- Required for issuing
      <div
        v-else-if="isText(component)"
        :ref="`textarea${component.id}`"
        :key="`textarea${component.id}text`"
        :disabled="!component.isEditable"
        tabindex="-1"
        cols="1"
        rows="1"
        :style="{
          resize: 'none',
          whiteSpace: 'pre',
          fontFamily: component.fontFamily,
          fontSize: `${reactiveFontSize(component.fontSize)}px`,
          fontStyle: component.isItalic ? 'italic' : 'normal',
          fontWeight: component.isBold ? 'bold' : 'normal',
          textDecoration: component.isUnderlined ? 'underline' : 'none',
          textAlign: component.textAlign,
          color: component.fontColor,
          opacity: 'visible' in component && !component.visible ? '0' : '1',
          margin: '0.5rem',
        }"
        @input="resizeTextArea(component, true)"
      >
        <input
          v-model="component.text"
          style="display: none"
          @input="resizeTextArea(component, true)"
        >
        {{ component.text }}
        {{ component.isEditable }}
      </div> -->
      {{ component.warp }}
      <Attribute
        v-model="componentsMappedPageSide[index]"
      />
    </div>
  </div>
</template>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&family=Covered+By+Your+Grace&family=Dawning+of+a+New+Day&family=Nothing+You+Could+Do&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anonymous+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap');

@font-face {
  font-family: 'Pixeled';
  font-weight: 400;
  font-style: normal;
  font-display: auto;
  unicode-range: U+000-5FF;
  src: url('../../assets/Fonts/Pixeled.ttf');
}

@font-face {
  font-family: 'PressStart2P';
  font-weight: 400;
  font-style: normal;
  font-display: auto;
  unicode-range: U+000-5FF;
  src: url('../../assets/Fonts/PressStart2P.ttf');
}

@font-face {
  font-family: 'Retro Gaming';
  font-weight: 400;
  font-style: normal;
  font-display: auto;
  unicode-range: U+000-5FF;
  src: url('../../assets/Fonts/Retro Gaming.ttf');
}

.page {
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: visible !important;
  background-color: none;
  border: 1px solid transparent;
}

.component {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  overflow: visible !important;
  cursor: default;
  min-width: 3.8rem;
  white-space: nowrap;
  transform: translate(-50%, -50%);
}

.activeComponent {
  border: 1px dashed #009fb1;
}

.component-options {
  display: flex;
  justify-content: flex-end;
  min-width: 3.5rem;
  position: absolute;
  top: 0%;
  left: 100%;
  transform: translate(-100%, -150%);
  overflow: visible !important;
}

.component-coordinates {
  font-size: 11px;
