<script>
import { useTemplateStore } from '@/store'
import { mapState } from 'pinia'

export default {
  props: {
    id: {
      type: Number,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
    styles: {
      type: Object,
      default: () => ({}),
    },
    visible: {
      type: Boolean,
      default: true,
    },
    isActive: {
      type: Boolean,
      default: false,
    },
    isEditable: {
      type: Boolean,
      default: true,
    },
    value: {
      type: String,
      required: true,
    },
    renderedDimensions: {
      type: Number,
      required: true,
    },
    size: {
      type: Number,
      default: 1,
    },
    src: {
      type: String,
      default: '',
    },
  },
  emits: ['input'],

  computed: {
    ...mapState(useTemplateStore, ['initialWidth']),
    reactiveFontSize() {
      return (this.renderedDimensions * this.styles.fontSize) // / this.initialWidth
    },
  },
  methods: {
  },
}
</script>

<template>
  <div>
    <!-- Signature Component -->
    <input
      :ref="`input${id}`"
      :value="value"

      type="text"
      :disabled="!isEditable"
      size="1"
      :style="{
        whiteSpace: 'pre',
        fontSize: `${reactiveFontSize}px`,
        fontFamily: `${styles.fontFamily}!important`,
        color: styles.fontColor,
      }"
      @input="$emit('input', value)"
    />
    <div
      v-if="src"
      :ref="`image${id}`"
      :style="{
        width: `${renderedDimensions * size}px`,
        resize:
          isEditable
            ? 'horizontal'
            : 'none',
      }"
    >
      <img
        :src="src"
        alt=""
        style="pointer-events: none"
        :style="{ width: '100%' }"
      />
    </div>
  </div>
</template>

<style>

</style>
