<script>
export default {
  name: 'CurvedText',
  props: {
    id: {
      type: Number,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
    warp: {
      type: Number,
      default: 0,
    },
    styles: {
      type: Object,
      default: () => ({}),
    },
    isEditable: {
      type: Boolean,
      default: true,
    },
    renderedDimensions: {
      type: Number,
      required: true,
    },
  },
  mounted() {
    console.log('CurvedText:mounted', this.styles)
  },
  methods: {
    reactiveFontSize(fontSize) {
      return fontSize * this.renderedDimensions
    },
  },
}
</script>

<template>
  <!-- Curved Text -->
  <div
    :ref="`textarea${id}`"
    style="overflow: visible !important"
    :style="{
      visibility:
        warp !== 0 && !isEditable ? 'visible' : 'hidden',
    }"
  >
    <svg
      :ref="`curvedtext${id}0`"
      viewBox="0 0 100 100"
      style="
            position: absolute;
            overflow: visible !important;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          "
    >
      <path
        id="curve"
        fill="transparent"
        stroke="#e1e1e1"
        stroke-width="3px"
        stroke-dasharray="10%"
        :d="
          warp > 0
            ? `M 50, ${
              50 + Math.abs(warp)
            }a ${
              Math.abs(warp)
            },${
              Math.abs(warp)
            } 0 1,1 0,${
              Math.abs(warp) * -2
            }a ${
              Math.abs(warp)
            },${
              Math.abs(warp)
            } 0 1,1 0, ${
              Math.abs(warp) * 2}`
            : `M 50, ${
              50 - Math.abs(warp)
            }a ${
              Math.abs(warp)
            },${
              Math.abs(warp)
            } 0 1,0 0,${
              Math.abs(warp) * 2
            }a ${
              Math.abs(warp)
            },${
              Math.abs(warp)
            } 0 1,0 0, ${
              Math.abs(warp) * -2}`
        "
      />

      <text
        :dy="warp > 0 ? '-10%' : '110%'"
        width="100"
        class="curved-text"
        :style="{
          fontFamily: `${styles.fontFamily} !important`,
          fontSize: `${reactiveFontSize(styles.fontSize)}px`,
          fontStyle: styles.isItalic ? 'italic' : 'normal',
          fontWeight: styles.isBold ? 'bold' : 'normal',
          textDecoration: styles.isUnderlined ? 'underline' : 'none',
          fill: styles.fontColor || styles.color,
          letterSpacing: `${styles.spacing}px`,
        }"
      >
        <textPath
          text-anchor="middle"
          startOffset="50%"
          xlink:href="#curve"
          :style="{
            fontFamily: `${styles.fontFamily} !important`,
          }"
        >
          {{ text }}
        </textPath>
      </text>
    </svg>
  </div>
</template>

<style>

</style>
