<script>
export default {
  props: {
    id: {
      type: Number,
      required: true,
    },
    renderedDimensions: {
      type: Number,
      required: true,
    },
    size: {
      type: Number,
      default: 1,
    },
    src: {
      type: String,
      default: '',
    },
    isEditable: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    reactiveWidth() {
      return this.size * this.renderedDimensions
    },
  },
  methods: {
  },

}
</script>

<template>
  <!-- Image Component -->
  <div
    :ref="`image${id}`"
    :style="{
      width: `${reactiveWidth}px`,
      resize:
        isEditable
          ? 'horizontal'
          : 'none',
    }"
  >
    <img
      :src="src"
      alt=""
      style="pointer-events: none"
      :style="{ width: '100%' }"
    />
  </div>
</template>

