<script>
export default {
  props: {
    id: {
      type: Number,
      required: true,
    },
    value: {
      type: String,
      required: true,
    },
    renderedDimensions: {
      type: Number,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
    styles: {
      type: Object,
      default: () => ({}),
    },
    visible: {
      type: Boolean,
      default: true,
    },
    isActive: {
      type: Boolean,
      default: false,
    },
    isEditable: {
      type: Boolean,
      default: true,
    },
  },
  emits: { input: null },
  computed: {
    reactiveFontSize() {
      return this.styles.fontSize * this.renderedDimensions
    },
  },
  methods: {
    update(value) {
      console.log('update', value)
      this.$emit('input', value)
    },
  },

}
</script>

<template>
  <!-- Text Component -->
  <textarea
    :ref="`textarea${id}`"
    :disabled="!isEditable"
    rows="1"
    :style="{
      resize: 'none',
      whiteSpace: 'pre',
      fontFamily: styles.fontFamily,
      fontSize: `${reactiveFontSize}px`,
      fontStyle: styles.isItalic ? 'italic' : 'normal',
      fontWeight: styles.isBold ? 'bold' : 'normal',
      textDecoration: styles.isUnderlined ? 'underline' : 'none',
      textAlign: styles.textAlign,
      color: styles.fontColor,
      opacity: visible ? '1' : '0',
      overflow: 'visible',
      width: 'auto',
      fieldSizing: 'content',
    }"
    :value="value"
    @input="update($event.target.value)"
  />
</template>

